    export let promoCode = "muzikanaletu";

    // export let levelBackgroundImage;
    export let levelHeadphonesImage;

    const textStore = isVictory ? victories : defeats;

    function getRandomTexts(store) {
        let min = 0;
        let max = store.length;

        let i = Math.floor(Math.random() * (max - min) + min);
        return store[i];
    }

    let { title, text } = getRandomTexts(textStore);

    onMount(() => {
        dataLayer.push({ event: "game_finish" });
    });
